.preloader {
  width: 100%;
  height: 100vh;
  background: $bg-color;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 10;
  display: grid;
  align-items: center;
  justify-content: center;

  &__title {
    letter-spacing: 20px;
    font-size: 15px;
    color: $white-color;
    font-weight: 700;
    position: relative;
    top: -50px;
    pointer-events: none;
    user-select: none;
    text-align: center;
  }
}
